
main {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  padding: 16px;
}

.card {
  padding: 16px;

  .card-content {
    display: flex;
    justify-content: center;
  }

  .card-text {
    margin: 0 0 16px 0;
    text-align: center;
    font-weight: bold;
  }
}


main > img {
  width: 100%;
}